.button-wrapper{
   height: 4em;
   display: flex;
   justify-content: center;
   align-items: center;
   font-weight: lighter;
   font-size: 1.4em;
   background-color: #e8e1e6;
   color: #888;
   flex: 1;
   outline: 1px solid #888;
}
.operator{
    background-color: #fe9241;
    color: white;
}
.input{
    height: 4em;
    width: 100%;
    background-color: black;
    color: white;
    outline: 1px solid #888;
}
.clear-btn{
    display: flex;
    height: 4em;
    width: 100%;
    background-color: #e8e1e6;
    justify-content: center;
    outline: 1px solid #888;
    color: #888 ;
    font-weight: lighter;
    align-items: center;
    flex: 1;
}